import { useEffect, useState } from "react";
import { fetchPdfData, fetchPdfMapData } from "../../services/pdfData";
import { getItem, StoreEnum } from "../../utils/store";

export interface IData {
    scaledScore: number,
    tableData1: number[],
    tableData2: number[],
    tableData3: number[],
    tableData4: number[],
    tableData5: number[],
    tableData6: number[],
    trendDate: string[],
    trendValue: number[][],
    lexileMeasure:string,
    testDate:string
}

export const usePdfData = () => {

    const [loading, setLoading] = useState(true);

    const [data, setData] = useState<IData>({
        tableData1: [],
        tableData2: [],
        tableData3:[],
        tableData4:[],
        tableData5:[],
        tableData6:[],
        scaledScore: 0,
        lexileMeasure:'',
        trendDate: [],
        trendValue:[],
        testDate: '',
    });
    useEffect(() => {
        let phone = getItem(StoreEnum.PHONE);
        if (__DEV__) {
            phone = '15895901300';
        }
        fetchPdfData(phone).then((res) => {
            if (res && res.executed) {
                setData({
                    scaledScore: res.scaled_score,
                    lexileMeasure: res.lexile_measure,
                    testDate: res.test_date,
                    tableData1: res.sub_items_alphabetic_principle,
                    tableData2: res.sub_items_phonemic_awareness,
                    tableData3: res.sub_items_phonics1,
                    tableData4: res.sub_items_phonics2,
                    tableData5: res.sub_items_structural_vocabulary,
                    tableData6: res.sub_items_other_domains,
                    trendDate: res.sub_domain_score_trend_date,
                    trendValue:res.sub_domain_score_trend_value
                })
            }
        }).finally(() => {
            setLoading(false);
        })
    }, []);

    return {
        data,
        loading
    }
}


export interface IMapData{
    url: string,
    rit: string,
    date: string,
    duration: string,
    option1X: string[],
    option1Y: string[],
    option2X: string[],
    option2Y:string[],
}

export const useMapUrl = () => {
    const [mapData, setMapData] = useState<IMapData>({
        url: '',
        rit: '',
        date: '',
        duration: '',
        option1X: [],
        option1Y: [],
        option2X: [],
        option2Y:[],
    });
    let phone = getItem(StoreEnum.PHONE);
    if (__DEV__) {
        phone = '15895901300';
    }
    useEffect(() => {
        fetchPdfMapData(phone).then((res) => {
            setMapData({
                url: res.map_pdf_url,
                rit: res.rit_score,
                date: res.test_date,
                duration: res.test_duration,
                option1X: res.map_score_trend_date,
                option1Y: res.map_score_trend_value,
                option2X: res.sub_domains_name,
                option2Y:res.sub_domains_score,
            });
        })
    }, []);
    return {
        mapData
    }
}