import ReactEcharts from "echarts-for-react"
import React from "react"
import { ColumnCenter, RowCenter } from "../styled"
import { HeaderWrap, HeaderItem, HeaderText, Line, SubTitle } from "./styled"
import { Document, Page ,pdfjs} from 'react-pdf';
import { IMapData } from "./action";
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;


export const Map:React.FC<{mapData:IMapData}> = ({mapData}) => {

    const mapOption2= {
        title: {
          text: '技能大项得分情况',
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {            // 坐标轴指示器，坐标轴触发有效
            type: 'line'        // 默认为直线，可选为：'line' | 'shadow'
          }
         },
        grid: { // 控制图的大小，调整下面这些值就可以，
          y2: 100,
          x2:200
        },
        xAxis: [{
          axisLabel: {
            interval: 0,//横轴信息全部显示  
            rotate: -10,//-15度角倾斜显示  
          },
          type: 'category',
          data: mapData.option2X,
        }],
        yAxis: [{
          type: 'value'
        }],
        series: [{
          name: 'Sub-Domains Score',
          type: 'bar',
          label: {
            normal: {
              show: true,
              position: 'top'
            }
          },
          barWidth: 30,
          itemStyle: {
            normal: {
              color: '#4ad2ff'
            }
          },
          data:mapData.option2Y,
        }]
      };
    
      const mapOption1 = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {            // 坐标轴指示器，坐标轴触发有效
            type: 'line'        // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        xAxis: {
          type: 'category',
          data: mapData.option1X
        },
        yAxis: {
          type: 'value'
        },
        series: [{
          data: mapData.option1Y,
          type: 'line',
        }]
      }
    
    const mapHeaderArr = [
        {
          label: 'RIT指标分值',
          value: mapData.rit || '-'
        },
        {
          label: '测试时长(minutes)',
          value: mapData.duration || '-'
        },
        {
          label: '测试日期',
          value: mapData.date || '-'
        },
      ];

    return (
        <ColumnCenter>
        <HeaderWrap style={{marginTop:10}}>
           {
             mapHeaderArr.map((v, i, arr) => (
              <HeaderItem
                key={i}
                style={i % 2 === 0 ? {} : { textAlign: 'right' }}
              >
                <span>
                  {v.label}
                </span>
                <HeaderText style={i === 1 ? { fontWeight:'bolder' } : {}}>
                  {v.value}
                </HeaderText>
               </HeaderItem>
             ))
           }
        </HeaderWrap>
        <Line></Line>
        <RowCenter>
          <div style={{marginRight:50,marginLeft:150}}>
          <SubTitle style={{ marginTop: 0 ,color:'#333',marginBottom:0,textAlign:'left'}}>RIT变化趋势</SubTitle>
          <ReactEcharts option={mapOption1 as echarts.EChartOption<echarts.EChartOption.Series>} theme="Imooc" style={{ height: '350px', width: '500px' }} />  
          </div>
         <ReactEcharts option={mapOption2 as echarts.EChartOption<echarts.EChartOption.Series>} theme="Imooc" style={{ height: '450px', width: '800px' }} />  
       </RowCenter>
        {
          mapData.url &&
          <div>

            <Document
              file={mapData.url}
            >
              <Page
                width={1400}
                pageNumber={1}
              />
            </Document>
          </div>
        } 
      </ColumnCenter>
    )
}


